<template>
  <main class="box-border h-full min-h-[calc(100vh-84px)] w-full pb-[84px]">
    <C-Back-Nav :title="`${GETTER_MECHANTNAME !== '' ? GETTER_MECHANTNAME + ' - ' : ''}預約`" :routeTarget="{name: 'Order'}"></C-Back-Nav>
    <section class="relative z-0 mt-[52px] p-3">

      <div class="mb-4 flex items-center justify-end">
        <div class=" flex items-center gap-2 text-left">
          <p class="">顯示過往預約紀錄</p>
          <div class="flex  flex-nowrap gap-1 text-left">
            <C-Checkbox-Full v-model="searchForm.isAccurate" class="!ml-0 !w-fit"></C-Checkbox-Full>
          </div>
        </div>
      </div>

      <div class="mb-4 flex items-center justify-between gap-2">
        <C-Input v-model="searchForm.keyword" @doFunction="getOrders('byClick')" :placeholder="'搜尋姓名、電話、代號'" class="mb-0 w-full">
          <template v-slot:endIcon>
            <font-awesome-icon v-if="searchForm.keyword !== ''" @click="doInitSearch" icon="fa-regular fa-xmark" class=" text-rose-600"/>
            <font-awesome-icon @click="getOrders('byClick')" icon="fa-regular fa-magnifying-glass"/>
          </template>
        </C-Input>
      </div>

      <div class="text-left text-sm">
        <p class="before:contents-[''] relative my-3 pl-2 before:absolute before:inset-0 before:h-full before:w-1 before:bg-primary">待審查({{isReviewResult}})</p>
        <div v-if="isReviewResult">

          <div v-for="(reviewItem, index) in reviewResult" :key="`reviewItem${index}`" @click="doRoute(reviewItem,true)" @keypress="doRoute(reviewItem,true)" class="mb-3 flex cursor-pointer items-center justify-between border-b border-solid border-primary/50 py-1 pl-2">
            <div class="w-3/12">
              <font-awesome-icon icon="fa-regular fa-calendar-days" class="mr-1" />
              <span>{{reviewItem.date | dateFmtROCMMDD}}</span>
              </div>
            <div class="w-3/12">
              <font-awesome-icon icon="fa-solid fa-clock-five" class="mr-1" />
              <span>{{reviewItem.date | dateFmtHm}}</span>
            </div>
            <div class="w-3/12 overflow-hidden text-ellipsis whitespace-nowrap">
              <font-awesome-icon icon="fa-solid fa-user" class="mr-1" />
              <span>{{reviewItem.customer.name || '--'}}</span>
              </div>
            <div class="w-3/12 min-w-[42px]">
              <span class="text-sucessText">{{reviewItem.status}}</span>
            </div>
            <div>
              <font-awesome-icon icon="fa-solid fa-angle-right" />
            </div>
          </div>
        </div>
        <div v-else class="my-3  pl-2">目前沒有待審查的預約</div>
      </div>

      <div class="text-left text-sm">
        <p class="before:contents-[''] relative my-3 pl-2 before:absolute before:inset-0 before:h-full before:w-1 before:bg-primary">預約明細({{isOrderResult}})</p>
        <div v-if="isOrderResult">

          <div v-for="(reviewItem, index) in orderResult" :key="`reviewItem${index}`" @click="doRoute(reviewItem,false)" @keypress="doRoute(reviewItem,false)" class="mb-3 flex cursor-pointer items-center justify-between border-b border-solid border-primary/50 py-1 pl-2">
            <div class="w-3/12">
              <font-awesome-icon icon="fa-regular fa-calendar-days" class="mr-1" />
              <span>{{reviewItem.date | dateFmtROCMMDD}}</span>
              </div>
            <div class="w-3/12">
              <font-awesome-icon icon="fa-solid fa-clock-five" class="mr-1" />
              <span>{{reviewItem.date | dateFmtHm }}</span>
            </div>
            <div class="w-3/12 overflow-hidden text-ellipsis whitespace-nowrap">
              <font-awesome-icon icon="fa-solid fa-user" class="mr-1" />
              <span>{{reviewItem.customer.name || '--'}}</span>
              </div>
            <div class="w-3/12 min-w-[42px]">
              <span>{{reviewItem.status}}</span>
            </div>
            <div>
              <font-awesome-icon icon="fa-solid fa-angle-right"/>
            </div>
          </div>
        </div>
        <div v-else class="my-3  pl-2">目前沒有任何預約</div>
      </div>

    </section>

  </main>
</template>

<script>
import { mapGetters, mapState } from 'vuex';

export default {
  name: 'order-index',
  description: '預約的首頁',
  metaInfo: {
    title: '名留',
    titleTemplate: '%s | 預約列表查詢',
  },
  mounted() {
    this.init();
  },
  data() {
    return {
      masks: {
        weekdays: 'WWW',
      },
      searchForm: {
        keyword: '', // 關鍵字搜尋
        // isAccurate: false, // false: 精準搜尋模式 ；true 模糊查詢 // 2022/09/12會議後調整
        isAccurate: true, // false: DESC ；true ASC // 用作顯示過往預約紀錄
        selectedDate: this.$dayjs(this.$dayjs().format('YYYY-MM-DD')).valueOf(),
      },

      monthkyOrdersDate: { // 以每月的訂單來放到arrts裡面
        start: this.$dayjs(new Date()).startOf('month').valueOf(),
        end: this.$dayjs(new Date()).endOf('month').valueOf(),
      },

      attrs: [], // 月曆點點 紅點代表有待審核

      reviewResult: [], // 待審核
      orderResult: [], // 預約成功、設計師已取消、已完成

      selectedAttribute: { // 日曆選定的日期樣式
        highlight: {
          style: {
            background: 'transparent',
          },
          fillMode: 'solid',
        },
      },
    };
  },
  methods: {
    async init() {
      await Promise.all([this.getMonthlyOrders(), this.getOrders()]).then(() => { });
    },
    async doInitSearch() {
      this.searchForm.keyword = '';
      await this.getOrders();
    },
    async getMonthlyOrders() {
      console.log('getMonthlyOrders');
      const res = await this.$api.orders({
        startDate: this.$dayjs(this.monthkyOrdersDate.start).isValid() ? this.$dayjs(this.monthkyOrdersDate.start).valueOf() : '',
        endDate: this.$dayjs(this.monthkyOrdersDate.end).isValid() ? this.$dayjs(this.monthkyOrdersDate.end).valueOf() : '',
        employeeId: this.userInfo.user.id,
        isReserved: true,
      });
      const { data, errors } = res.data;
      if (errors) return {};
      this.attrs = Array.isArray(data.orders.orders) ? data.orders.orders.flatMap((orderItem) => (orderItem.status === '待審核' ? [
        {
          key: `${orderItem.id}`,
          // dot: {
          //   color: 'red',
          //   fillMode: 'solid',
          // },
          customData: {
            class: 'red',
          },
          dates: orderItem.date,
        },
      ] : [
        {
          key: `${orderItem.id}`,
          // dot: {
          //   style: {
          //     background: 'red',
          //   },
          //   fillMode: 'solid',
          // },
          dates: orderItem.date,
        },
      ])) : [];
      this.$forceUpdate();
      return res;
    },
    async getOrders() {
      const defaultStarDate = this.$dayjs(this.searchForm.selectedDate).isValid() ? this.$dayjs(this.searchForm.selectedDate).valueOf() : '';
      const res = await this.$api.orders({
        startDate: this.searchForm.isAccurate ? null : defaultStarDate,
        endDate: null,
        employeeId: this.userInfo.user.id,
        orderKeyword: {
          keyword: typeof this.searchForm.keyword === 'string' ? this.searchForm.keyword.toLocaleLowerCase() : this.searchForm.keyword,
          isFuzzySearch: true, // 因為api跟畫面上是相反的
        },
        sortWay: this.searchForm.isAccurate ? 'ASC' : 'DESC',
        isReserved: true,
      });
      const { data, errors } = res.data;
      if (errors) return {};
      this.reviewResult = data.orders.orders.filter((order) => order.status === '待審核');
      this.orderResult = data.orders.orders.filter((order) => order.status !== '待審核');
      console.log(this.reviewResult);
      console.log(this.orderResult);
      this.$forceUpdate();
      return res;
    },
    doRoute(item, isReview) {
      this.$router.push({ name: 'Order-edit', query: { id: item.id, isReview } });
    },
    preNextMonth(num) {
      this.monthkyOrdersDate.start = this.$dayjs(this.$dayjs(this.monthkyOrdersDate.start).add(num, 'month')).startOf('month').format('YYYY-MM-DD');
      this.monthkyOrdersDate.end = this.$dayjs(this.$dayjs(this.monthkyOrdersDate.end).add(num, 'month')).endOf('month').format('YYYY-MM-DD');
    },
    swipeMonth(e) {
      const y = e.year;
      const m = e.month - 1;
      const firstDay = new Date(y, m, 1);
      const lastDay = new Date(y, m + 1, 0);
      const starDate = this.$dayjs(firstDay).format('YYYY-MM-DD');
      const endDate = this.$dayjs(lastDay).format('YYYY-MM-DD');

      this.monthkyOrdersDate.start = starDate;
      this.monthkyOrdersDate.end = endDate;
    },
  },
  computed: {
    ...mapState('userInfo', ['userInfo', 'currentMechantId']),
    ...mapGetters('userInfo', ['GETTER_MECHANTNAME']),
    isReviewResult() {
      return this.reviewResult.length;
    },
    isOrderResult() {
      return this.orderResult.length;
    },
  },
  watch: {
    'searchForm.selectedDate': {
      handler() {
        if (!this.searchForm.selectedDate) return;
        this.getOrders();
      },
    },
    'searchForm.isAccurate': {
      handler() {
        if (!this.searchForm.selectedDate) return;
        this.getOrders();
      },
    },
    monthkyOrdersDate: {
      deep: true,
      handler() {
        this.getMonthlyOrders();
      },
    },
  },
};
</script>

<style lang="scss" scoped>
